import { fetchRecipes } from "./fetchData";
import { getActiveCategory } from "./categoriesDisplay";

const container = document.getElementById("recipe-results");

// Search input reference
const searchInput = document.getElementById("search-recipes");

export async function updateRecipes() {
  const allRecipes = await fetchRecipes();
  const searchQuery = searchInput?.value?.trim().toLowerCase() || "";
  const selectedCategory = getActiveCategory();

  let filtered = allRecipes;

  // Filter by category
  if (selectedCategory) {
    filtered = filtered.filter((r) => r.category === selectedCategory);
  }

  // Filter by search input
  if (searchQuery) {
    filtered = filtered.filter((r) =>
      r.title.toLowerCase().includes(searchQuery)
    );
  }

  renderRecipes(filtered);
}

function renderRecipes(recipes) {
  container.innerHTML = "";

  if (!recipes || recipes.length === 0) {
    container.innerHTML = "<p>No recipes found.</p>";
    return;
  }

  recipes.forEach((recipe) => {
    const item = document.createElement("div");
    item.classList.add("recipe-card");

    // Create the button and image
    const button = document.createElement("button");
    button.classList.add("favorites-button");

    // Create the SVG heart icon element
    const heart = document.createElementNS("http://www.w3.org/2000/svg", "svg");
    heart.setAttribute("xmlns", "http://www.w3.org/2000/svg");

    const path = document.createElementNS("http://www.w3.org/2000/svg", "path");
    path.setAttribute(
      "d",
      "M12 21.35l-1.45-1.32C5.4 15.36 2 12.28 2 8.5 2 5.42 4.42 3 7.5 3c1.74 0 3.41.81 4.5 2.09C13.09 3.81 14.76 3 16.5 3 19.58 3 22 5.42 22 8.5c0 3.78-3.4 6.86-8.55 11.54L12 21.35z"
    );
    path.setAttribute("fill", "white");

    heart.appendChild(path);
    button.appendChild(heart);

    // Add a click event listener to toggle heart color
    button.addEventListener("click", function () {
      if (path.getAttribute("fill") === "white") {
        path.setAttribute("fill", "red");
      } else {
        path.setAttribute("fill", "white");
      }
    });

    const title = document.createElement("h4");
    title.classList.add("recipe-title");
    title.textContent = recipe.title;

    const recipeImg = document.createElement("img");
    recipeImg.classList.add("recipe-img");
    recipeImg.src = recipe.thumb;
    recipeImg.alt = recipe.title;

    const description = document.createElement("p");
    description.classList.add("card-description");
    description.textContent = recipe.description;

    const detailsContainer = document.createElement("div");
    detailsContainer.classList.add("details-card-container");
    // detailsContainer.appendChild(button);
    detailsContainer.appendChild(title);
    detailsContainer.appendChild(description);

    // item.appendChild(button);
    item.appendChild(recipeImg);
    item.appendChild(button);
    item.appendChild(detailsContainer);

    // Append the recipe card to the container
    container.appendChild(item);
  });
}
